.wrapper {
    max-width: 300px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 7px;
    padding-bottom: 23px;
    padding-left: 20px;
    padding-right: 20px;
    background-color:#fff;
    border-radius: 15px;
    box-shadow: 0 10px 10px 0 rgba(0,0,0,0.1);
    box-sizing: border-box;
}

.title {
    margin: 0 0 10px;
    color: #5e5d5e;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    text-decoration: underline;
    @media(min-width: 600px) {
        margin: 5px 0 15px;
    }
}

