.table {
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
}

.row {
    display: flex;
}

.cell {
    width: 70px;
    height: 70px;
    border: 2px solid rgb(107, 106, 107);
    cursor: pointer;
    background-color: #fff;
}
.row:first-child .cell {
    border-top-width: 4px;
}

.row:last-child .cell {
    border-bottom-width: 4px;
}

.row .cell:first-child {
    border-left-width: 4px;
}

.row .cell:last-child {
    border-right-width: 4px;
}

.row:first-child .cell:first-child {
    border-top-left-radius: 10px;
}

.row:first-child .cell:last-child {
    border-top-right-radius: 10px;
}
.row:last-child .cell:first-child {
    border-bottom-left-radius: 10px;
}

.row:last-child .cell:last-child {
    border-bottom-right-radius: 10px;
}

.row:nth-child(2n + 1) {
    .cell:nth-child(2n) {
        background-color: rgba(120,120,120,0.3);
    }
}
.row:nth-child(2n) {
    .cell:nth-child(2n + 1) {
        background-color: rgba(120,120,120,0.3);
    }
}

.btn {
    padding: 15px 20px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: rgb(49, 69, 182);
    color: #fff;
    font-size: 1em;
    font-weight: 400;
    font-family: inherit;
    text-transform: uppercase;
    cursor: pointer;
}
.btn:hover {
    background-color: rgb(35, 52, 146);
}

.message {
    max-width: 296px;
    background: rgba(203, 251, 240, 0.5);
    border: 1px solid rgba(73, 221, 93, 0.8);
    border-radius: 5px;
    color: rgb(73, 221, 93);
    font-size: 16px;
    font-weight: 500;
    margin: 0;
    margin-top: 0;
    margin-bottom: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
    text-align: center;
}

.cross {
    background-size: 80% auto;
    background-position: center center;
    background-image: url('../../static/close.svg');
    background-repeat: no-repeat;
}
.zero {
    background-size: 80% auto;
    background-position: center center;
    background-image: url('../../static/icon.svg');
    background-repeat: no-repeat;
}

.winner {
    background-color: rgb(127, 241, 127) !important;
}